    const deleteMenu = async (menu) => {
  const res = await showConfirmation(
    'Delete Menu',
    'Confirm Deleting Menu ' + menu.name,
    `/menus/${menu.id}`,
    'delete'
  )
  if (res?.refresh) getHeaders()
}

    const deleteChildMenu = async (menu) => {
  const res = await showConfirmation(
    'Delete Child Menu',
    'Confirm Deleting Child Menu ' + menu.name,
    `/child-menus/${menu.id}`,
