    'delete'
  )
  if (res?.refresh) getHeaders()
}


    const getHeaders = () => {
    loading_spinner.value = true;
    axiosInstance.get('/menus').then((response) => {
        headers.value = response.data.map((item, index) => ({
            ...item,
            SNo: index + 1,
            child_menus: item.child_menus.map((child, childIndex) => ({
                ...child,
                SNo: childIndex + 1,
                sub_child_menus: child.sub_child_menus.map((subChild, subChildIndex) => ({
                    ...subChild,
                    SNo: subChildIndex + 1,
                })),
            })),
        }));
        loading_spinner.value = false;
    }).catch(error => {
        showError(error.message);
        loading_spinner.value = false;
    });
};

    
    const view_row = (row) => {
