    sub_child_menus.value = row.sub_child_menus || [];
    };

const view_child_row = (row) => {
    if (selectedSubMenu.value && selectedSubMenu.value.id === row.id) {
        selectedSubMenu.value = null;      
        parent_menu_id.value = null;       
        sub_child_menus.value = [];        
    } else {
        selectedSubMenu.value = row;       
        parent_menu_id.value = row.id;     
        sub_child_menus.value = row.sub_child_menus || [];
    }
};

