           if (clearSelection) {
        selectedSubMenu.value = null;  
        sub_child_menus.value = [];    
    }
    };
  
     const updateHeader = async () => {
        loading.value = true;
        const payload = {
            position : position.value,
            name: name.value,
            permission: permission.value,
            path: path.value,
            svg: svg.value,
        };
          try {
            const response = await axiosInstance.put(`/menus/${id.value}`, payload);
            getHeaders();
            closeEditModal();
            showMessage('Header updated successfully!');
            document.getElementById('close_editing').click();
        } catch (error) {
            loading.value = false;
            showError(error.message);
            console.error(error);
        } finally{
    loading.value = false;
        } 
    };

   const updateSubMenu = async () => {
    loading.value = true;
    const payload = {
        name: name.value,
        permission: permission.value,
        path: path.value,
        position: position.value
    };
    try {
        const response = await axiosInstance.put(`/child-menus/${id.value}`, payload);
        getHeaders();
        closeEditModal(true);
        showMessage('Sub Menu updated successfully!');
        document.getElementById('close_menu').click();
    } catch (error) {
        loading.value = false;
        showError(error.message);
        console.error(error);
    } finally{
    loading.value = false;
        } 
    };
  
  const registerMenu = async () => {    
    loading_spinner.value = true;

    const itemData = {
        name: name.value,
        permission: permission.value,
        path: path.value,
        svg: svg.value,
        position: position.value
    };

    try {
        const response = await axiosInstance.post('/menus', itemData);
        getHeaders();
        showMessage('Menu registered successfully!');
        resetForm();
    } catch (error) {
        showError(error.message);
        console.error(error.message);
    } finally {
        loading_spinner.value = false;
    }
};

   const resetForm = () => {
    name.value = '';
    permission.value = '';
    path.value = '#';
    svg.value = '';
    position.value = '';
    menu_registration.value = false;
};

const resetMenu = () => {
    name.value = '';
    permission.value = '';
    path.value = '#';
    position.value = '';
    child_menu_registration.value = false;
};

const resetChild = () => {
    name.value = '';
    permission.value = '';
    path.value = '#';
    position.value = '';
    sub_child_registration.value = false;
};

const closeViewModal = () => {
    selectedSubMenu.value = null;
